import React, {useEffect, useState} from 'react';
import Header from "../../../components/header/Header";
import {useParams} from "react-router-dom";
import {GET_SCHEDULE_UNIT} from "../../../api/operations/queries/schedule";
import moment from "moment";
import DayTabs from "./dayTabs/DayTabs";
import {client} from "../../../api/client";
import ScheduleTimeline from "./scheduleTimeline/ScheduleTimeline";
import styles from './scheduleAdmin.module.css';
import {isDateInRange} from "../../../helpers/scheduleHelpers";

const DAY_OF_WEEKS = ['', '', '', '', '\'', '', ''];
const daysAmount = 365 * 2;

const AdminScheduleClassroom = () => {
  const {classroomName}: any = useParams();
  const [schedule, setSchedule] = useState([]);
  const [chosenDay, setChosenDay] = useState(0);
  const currentDay = moment().day();
  const daysArray = new Array(daysAmount).fill(null);

  useEffect(() => {
    client.query({
      query: GET_SCHEDULE_UNIT,
      variables: {
        classroomName,
        date: moment().add(chosenDay - currentDay, 'days').toISOString(),
      }
    }).then(result => {
      setSchedule(result.data.schedule);
    });
  }, [chosenDay]);

  return (
    <div>
      <Header>
        <h1>    {classroomName}</h1>
      </Header>
      <DayTabs onChange={setChosenDay} chosenDay={chosenDay} days={DAY_OF_WEEKS}/>
      {JSON.stringify(schedule)}
      <div className={styles.wrapper}>
        {daysArray.map((_, index) => {
          const currentItemDate = moment().set('dayOfYear', 1).add(index, 'days');
          const currentItemDateFormatted = currentItemDate.format('DD.MM.YYYY');
          return (
            <ScheduleTimeline
              date={currentItemDateFormatted}
              isInRange={isDateInRange(currentItemDate, schedule[0]?.dateStart, schedule[0]?.dateEnd)}
              schedule={schedule}
            />
          )
        })}
      </div>
    </div>
  );
};

export default AdminScheduleClassroom;
