import React, {FC, useState} from 'react';
import styles from './scheduleTimeline.module.css';
import {WORKING_DAY_END, WORKING_DAY_START} from "../../../../helpers/constants";
import {ScheduleUnitType} from "../../../../models/models";

type PropTypes = {
  date: string;
  isInRange: boolean;
  schedule: ScheduleUnitType[];
}

const DAY_LENGTH = WORKING_DAY_END - WORKING_DAY_START;
const hours = new Array(24).fill(true, WORKING_DAY_START, WORKING_DAY_END)
  .map((item, index) => item ? index : false).filter(item => item);

const ScheduleTimeline: FC<PropTypes> = ({date, isInRange, schedule}) => {
  const [from, setFrom] = useState(+schedule[0].from.split(':')[0]);
  const [to, setTo] = useState(+schedule[0].to.split(':')[0]);

  return (
    <div
      className={[isInRange ? styles.filled : styles.empty, styles.row].join(' ')}
      style={{gridTemplateColumns: `100px repeat(${DAY_LENGTH}, 1fr)`}}
    >
      <span>
        {date}
      </span>
      {(hours as number[]).map((item) => (
        <span className={(item >= from && item <= to) ? styles.filled : styles.empty}></span>
      ))}
    </div>
  );
};

export default ScheduleTimeline;
